// frontend/src/app/admin/jobs/JobsManagement.tsx
"use client";

import React, { useCallback, useEffect, useState } from "react";
import axios from "axios";
import { FiEdit, FiTrash2, FiPlus } from "react-icons/fi";
import { useRouter } from "next/navigation";
import Modal from "../../components/Modal";

// ---------------------- Types ----------------------
interface Job {
  _id: string;
  title: string;
  slug?: string;
  location: string;
  type: string;
  status: string;
  description: string;
  requirements?: string[];
  deadline?: string;
  createdAt: string;
}

interface JobFormState {
  title: string;
  slug: string;
  location: string;
  type: string;
  description: string;
  requirements: string; // multiline string (one per line)
  deadline: string; // ISO date string or empty
}

interface ApiErrorData {
  message?: string;
}

// ---------------------- Component ----------------------
const JobsManagement: React.FC = () => {
  const router = useRouter();

  const [jobs, setJobs] = useState<Job[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [search, setSearch] = useState<string>("");
  const [modalOpen, setModalOpen] = useState<boolean>(false);
  const [editingJob, setEditingJob] = useState<Job | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [saving, setSaving] = useState<boolean>(false);

  // form state (used when modal open)
  const [formState, setFormState] = useState<JobFormState>({
    title: "",
    slug: "",
    location: "",
    type: "Full-time",
    description: "",
    requirements: "",
    deadline: "",
  });

  // ---------------------- Fetch Jobs ----------------------
  const fetchJobs = useCallback(async () => {
    setLoading(true);
    setError(null);
    try {
      const res = await axios.get<{ success: boolean; data: Job[] }>("/api/admin/jobs", {
        withCredentials: true,
      });
      setJobs(res.data.data || []);
    } catch (err: unknown) {
      if (axios.isAxiosError(err)) {
        const apiData = err.response?.data as ApiErrorData | undefined;
        if (err.response?.status === 401) {
          router.push("/admin/login");
          return;
        }
        setError(apiData?.message || err.message || "Failed to fetch jobs.");
      } else if (err instanceof Error) {
        setError(err.message);
      } else {
        setError("An unknown error occurred while fetching jobs.");
      }
    } finally {
      setLoading(false);
    }
  }, [router]);

  useEffect(() => {
    fetchJobs();
  }, [fetchJobs]);

  // ---------------------- Modal / Form handlers ----------------------
  const openCreateModal = () => {
    setEditingJob(null);
    setFormState({
      title: "",
      slug: "",
      location: "",
      type: "Full-time",
      description: "",
      requirements: "",
      deadline: "",
    });
    setError(null);
    setModalOpen(true);
  };

  const openEditModal = (job: Job) => {
    setEditingJob(job);
    setFormState({
      title: job.title,
      slug: job.slug || "",
      location: job.location,
      type: job.type,
      description: job.description,
      requirements: (job.requirements || []).join("\n"),
      deadline: job.deadline || "",
    });
    setError(null);
    setModalOpen(true);
  };

  const closeModal = () => {
    setModalOpen(false);
    setEditingJob(null);
    setError(null);
  };

  // ---------------------- Delete ----------------------
  const handleDelete = async (jobId: string) => {
    const ok = confirm("Are you sure you want to delete this job?");
    if (!ok) return;

    setError(null);
    try {
      await axios.delete(`/api/admin/jobs/${jobId}`, { withCredentials: true });
      setJobs((prev) => prev.filter((j) => j._id !== jobId));
    } catch (err: unknown) {
      if (axios.isAxiosError(err)) {
        const apiData = err.response?.data as ApiErrorData | undefined;
        setError(apiData?.message || err.message || "Delete failed");
      } else if (err instanceof Error) {
        setError(err.message);
      } else {
        setError("Unknown error during delete.");
      }
    }
  };

  // ---------------------- Submit (Create / Update) ----------------------
  const handleSubmit = async (e?: React.FormEvent) => {
    if (e) e.preventDefault();
    setError(null);

    // client-side validation
    if (!formState.title.trim() || !formState.location.trim() || !formState.description.trim()) {
      setError("Please fill in Title, Location and Description.");
      return;
    }

    setSaving(true);
    try {
      const payload = {
        title: formState.title.trim(),
        slug: formState.slug.trim() || undefined,
        location: formState.location.trim(),
        type: formState.type,
        description: formState.description.trim(),
        deadline: formState.deadline || undefined,
        requirements: formState.requirements
          .split("\n")
          .map((r: string) => r.trim())
          .filter(Boolean),
      };

      if (editingJob) {
        const res = await axios.patch<{ success: boolean; data: Job }>(
          `/api/admin/jobs/${editingJob._id}`,
          payload,
          { withCredentials: true }
        );
        setJobs((prev) => prev.map((j) => (j._id === editingJob._id ? res.data.data : j)));
      } else {
        const res = await axios.post<{ success: boolean; data: Job }>(
          "/api/admin/jobs",
          payload,
          { withCredentials: true }
        );
        setJobs((prev) => [res.data.data, ...prev]);
      }

      closeModal();
    } catch (err: unknown) {
      if (axios.isAxiosError(err)) {
        const apiData = err.response?.data as ApiErrorData | undefined;
        setError(apiData?.message || err.message || "Save job failed");
      } else if (err instanceof Error) {
        setError(err.message);
      } else {
        setError("Unknown error while saving job.");
      }
    } finally {
      setSaving(false);
    }
  };

  // ---------------------- Filtered list ----------------------
  const filteredJobs = jobs.filter((job) => {
    const q = search.trim().toLowerCase();
    if (!q) return true;
    return (
      job.title.toLowerCase().includes(q) ||
      job.location.toLowerCase().includes(q) ||
      (job.slug || "").toLowerCase().includes(q)
    );
  });

  // ---------------------- Render ----------------------
  return (
    <div>
      <div className="flex items-center justify-between mb-4">
        <div>
          <h1 className="text-2xl font-bold">Jobs Management</h1>
          <p className="text-sm text-slate-500">Create, edit or remove job postings.</p>
        </div>

        <div className="flex items-center gap-3">
          <input
            type="text"
            placeholder="Search by title, location or slug..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="border p-2 rounded w-64"
            aria-label="Search jobs"
          />
          <button
            onClick={openCreateModal}
            className="flex items-center bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
            aria-label="Create new job"
          >
            <FiPlus className="mr-2" /> New Job
          </button>
        </div>
      </div>

      {error && <div className="text-red-600 mb-3">{error}</div>}

      {loading ? (
        <div className="py-6 text-center text-gray-600">Loading jobs...</div>
      ) : filteredJobs.length === 0 ? (
        <div className="py-6 text-center text-gray-600">No jobs found.</div>
      ) : (
        <div className="overflow-x-auto bg-white rounded shadow">
          <table className="w-full table-auto">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-4 py-2 text-left text-sm font-medium">Title</th>
                <th className="px-4 py-2 text-left text-sm font-medium">Location</th>
                <th className="px-4 py-2 text-left text-sm font-medium">Type</th>
                <th className="px-4 py-2 text-left text-sm font-medium">Status</th>
                <th className="px-4 py-2 text-center text-sm font-medium">Actions</th>
              </tr>
            </thead>
            <tbody>
              {filteredJobs.map((job) => (
                <tr key={job._id} className="border-t">
                  <td className="px-4 py-3">{job.title}</td>
                  <td className="px-4 py-3">{job.location}</td>
                  <td className="px-4 py-3">{job.type}</td>
                  <td className="px-4 py-3 capitalize">{job.status}</td>
                  <td className="px-4 py-3 text-center">
                    <div className="inline-flex items-center gap-3">
                      <button
                        onClick={() => openEditModal(job)}
                        className="text-blue-600 hover:text-blue-800"
                        title="Edit job"
                        aria-label={`Edit ${job.title}`}
                      >
                        <FiEdit />
                      </button>
                      <button
                        onClick={() => handleDelete(job._id)}
                        className="text-red-600 hover:text-red-800"
                        title="Delete job"
                        aria-label={`Delete ${job.title}`}
                      >
                        <FiTrash2 />
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {modalOpen && (
        <Modal title={editingJob ? "Edit Job" : "New Job"} onClose={closeModal}>
          <form onSubmit={handleSubmit} className="space-y-3">
            <input
              type="text"
              placeholder="Title *"
              value={formState.title}
              onChange={(e) => setFormState((s) => ({ ...s, title: e.target.value }))}
              className="w-full border p-2 rounded"
              required
            />
            <input
              type="text"
              placeholder="Slug (optional)"
              value={formState.slug}
              onChange={(e) => setFormState((s) => ({ ...s, slug: e.target.value }))}
              className="w-full border p-2 rounded"
            />
            <input
              type="text"
              placeholder="Location *"
              value={formState.location}
              onChange={(e) => setFormState((s) => ({ ...s, location: e.target.value }))}
              className="w-full border p-2 rounded"
              required
            />
            <select
              value={formState.type}
              onChange={(e) => setFormState((s) => ({ ...s, type: e.target.value }))}
              className="w-full border p-2 rounded"
            >
              <option>Full-time</option>
              <option>Part-time</option>
              <option>Contract</option>
              <option>Internship</option>
              <option>Temporary</option>
            </select>
            <textarea
              placeholder="Description *"
              value={formState.description}
              onChange={(e) => setFormState((s) => ({ ...s, description: e.target.value }))}
              className="w-full border p-2 rounded h-28"
              required
            />
            <textarea
              placeholder="Requirements (one per line)"
              value={formState.requirements}
              onChange={(e) => setFormState((s) => ({ ...s, requirements: e.target.value }))}
              className="w-full border p-2 rounded h-24"
            />
            <label className="block">
              <span className="text-sm text-slate-700">Application Deadline (optional)</span>
              <input
                type="date"
                value={formState.deadline}
                onChange={(e) => setFormState((s) => ({ ...s, deadline: e.target.value }))}
                className="mt-1 w-full border p-2 rounded"
              />
            </label>

            <div className="flex gap-3 justify-end pt-2">
              <button
                type="button"
                onClick={closeModal}
                className="px-4 py-2 rounded border"
                disabled={saving}
              >
                Cancel
              </button>
              <button
                type="submit"
                className="px-4 py-2 rounded bg-blue-600 text-white hover:bg-blue-700"
                disabled={saving}
              >
                {saving ? "Saving..." : editingJob ? "Update Job" : "Create Job"}
              </button>
            </div>
          </form>
        </Modal>
      )}
    </div>
  );
};

export default JobsManagement;
