// frontend/src/app/admin/jobs/page.tsx
"use client";

import React, { useEffect, useState } from "react";
import axios, { AxiosError } from "axios";
import { useRouter } from "next/navigation";
import Layout from "../layout";
import JobsManagement from "./JobsManagement";

const JobsPage: React.FC = () => {
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  const [authenticated, setAuthenticated] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const checkAuth = async () => {
      try {
        setLoading(true);
        const res = await axios.get("/api/admin/me", { withCredentials: true });

        if (!res.data?.success) {
          router.push("/admin/login");
          return;
        }

        setAuthenticated(true);
      } catch (err: unknown) {
        if (axios.isAxiosError(err)) {
          const axiosError = err as AxiosError<{ message?: string }>;
          setError(axiosError.response?.data?.message || "Failed to verify admin session.");
        } else if (err instanceof Error) {
          setError(err.message);
        } else {
          setError("Unexpected error occurred while verifying admin session.");
        }
      } finally {
        setLoading(false);
      }
    };

    checkAuth();
  }, [router]);

  if (loading) {
    return (
      <Layout>
        <div className="flex items-center justify-center h-[70vh] text-gray-500">
          Checking admin session...
        </div>
      </Layout>
    );
  }

  if (error) {
    return (
      <Layout>
        <div className="flex items-center justify-center h-[70vh] text-red-500">
          {error}
        </div>
      </Layout>
    );
  }

  if (!authenticated) {
    return null; // Redirect already triggered
  }

  return (
    <Layout>
      <div className="p-6">
        <h1 className="text-3xl font-bold mb-6">Manage Jobs</h1>
        <JobsManagement />
      </div>
    </Layout>
  );
};

export default JobsPage;
